import * as THREE from "three";
// 
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import Ocean from "./Ocean";
import Sky from "./Sky";

function initThree() {
	const scene = new THREE.Scene();
	scene.fog = new THREE.Fog(0x88ccee, 0, 5);

	const camera = new THREE.PerspectiveCamera(
		75,
		window.innerWidth / window.innerHeight,
		0.1,
		1000
	);

	// 
	camera.position.set(0, 0, 20);
	scene.add(camera);

	// 
	const renderer = new THREE.WebGLRenderer({antialias: true});
	renderer.shadowMap.enabled = true;
	renderer.shadowMap.type = THREE.VSMShadowMap;
	renderer.outputEncoding = THREE.sRGBEncoding;
	renderer.toneMapping = THREE.ACESFilmicToneMapping;
	renderer.toneMappingExposure = 0.8;
	renderer.setSize(window.innerWidth, window.innerHeight);
	document.body.appendChild(renderer.domElement);

	// 
	const controls = new OrbitControls(camera, renderer.domElement);
	controls.enableDamping = true;
	controls.maxPolarAngle = Math.PI / 2.2;
	controls.minDistance = 1;
	controls.maxDistance = 20;

	// 
	// const axesHelper = new THREE.AxesHelper(5)
	// scene.add(axesHelper)

	const clock = new THREE.Clock()

	// 
	window.addEventListener("resize", () => {
		// 
		camera.aspect = window.innerWidth / window.innerHeight;
		//   
		camera.updateProjectionMatrix();
		//   
		renderer.setSize(window.innerWidth, window.innerHeight);
		//   
		renderer.setPixelRatio(window.devicePixelRatio);
	});

	return {
		scene,
		camera,
		renderer,
		controls,
		clock,
		stats
	}
}

const {
	scene,
	camera,
	renderer,
	controls,
	clock,
	stats
} = initThree();

// 
// const directionLight = new THREE.DirectionalLight('#ff9900',10);
// directionLight.castShadow = true;
// directionLight.position.set(0, 10, 10)
// scene.add(directionLight)
const position = new THREE.Vector3(0, 10, -10)

const plane = new THREE.Mesh(
	new THREE.PlaneGeometry(500, 500)
)
plane.rotation.x = -Math.PI / 2
const ocean = new Ocean(plane, camera, position)
ocean.scale = 0.025
scene.add(plane)

const sky = new Sky(camera, scene)

function render() {
	const delta = clock.getDelta();
	controls.update();
	renderer.render(scene, camera);
	// render
	requestAnimationFrame(render);
	ocean.update(ocean.scale)
	sky.update(delta)
}

render();
